*,
*::after,
*::before {
    outline: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

html {
    width: 100%;
    height: 100%;
}

body {
    background: #f5f5f5;
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-size: 12px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

h1 {
    color: #112a46;
    margin: 0 0 30px;
    font-size: 30px;
    font-weight: 600;
}

h2 {
    color: #000;
    font-size: 22px;
    font-weight: bold;
}

h3 {
    color: #585858;
    font-size: 14px;
    font-weight: bold;
    line-height: 19px;
    margin: 0 0 20px;
}

button {
    border-radius: 0;
    padding: 0;
}

input {
    background-color: #fff;
}
