/**
  Variables are named after http://chir.ag/projects/name-that-color/
  ATTENTION ATTENTION: Please order this list alphabetically
*/

.toggler--1POkRE5ISk {
    width: 37px;
    height: 20px;
}

.toggler--1POkRE5ISk input + span {
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        background: #fff;
        border: 1px solid #ccc;
        border-radius: 10px;
        -webkit-transition: background 200ms, border-color 200ms;
        transition: background 200ms, border-color 200ms;
    }

.toggler--1POkRE5ISk input + span::after {
            content: '';
            position: absolute;
            top: -1px;
            left: -1px;
            display: block;
            width: 20px;
            height: 20px;
            border-radius: 10px;
            background: #fff;
            border: 1px solid #ccc;
            -webkit-transition: left 200ms, border-color 200ms;
            transition: left 200ms, border-color 200ms;
        }

.toggler--1POkRE5ISk input:checked + span {
        background: #112a46;
        border-color: #112a46;
    }

.toggler--1POkRE5ISk input:checked + span::after {
            border-color: #112a46;
            left: 16px;
        }

.toggler--1POkRE5ISk input:disabled + span {
        background: transparent;
        border-color: #ccc;
    }

.toggler--1POkRE5ISk input:disabled + span::after {
            border-color: #ccc;
            background-color: transparent;
        }
