/**
  Variables are named after http://chir.ag/projects/name-that-color/
  ATTENTION ATTENTION: Please order this list alphabetically
*/

.tab--2W8ckbdp2h {
    position: relative;
}

.tab--2W8ckbdp2h::after {
        content: '';
        width: calc(100% - 40px);
        height: 2px;
        background-color: #000;
        position: absolute;
        left: 20px;
        bottom: 0;
        -webkit-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transition: -webkit-transform .15s ease-in-out;
        transition: -webkit-transform .15s ease-in-out;
        transition: transform .15s ease-in-out;
        transition: transform .15s ease-in-out, -webkit-transform .15s ease-in-out;
    }

.tab--2W8ckbdp2h button {
        height: 40px;
        cursor: pointer;
        padding: 10px 20px;
        color: #000;
        background-color: transparent;
        font-size: 12px;
        border: 0;
    }

/* Needed because we want to prevent the jump on font-weight changes */

.tab--2W8ckbdp2h button::after {
            display: block;
            content: attr(title);
            font-weight: bold;
            height: 1px;
            color: transparent;
            overflow: hidden;
            visibility: hidden;
        }

.tab--2W8ckbdp2h.selected--1rDuppsoTN button {
            font-weight: bold;
            cursor: default;
        }

.tab--2W8ckbdp2h.selected--1rDuppsoTN::after {
            -webkit-transform: scaleX(1);
                    transform: scaleX(1);
        }
