.checkbox--23OsqqyZ9T {
    width: 16px;
    height: 16px;
}

.checkbox--23OsqqyZ9T input + span {
        display: block;
        width: 100%;
        height: 100%;
        background: transparent;
        position: absolute;
        top: 0;
        left: 0;
        border-radius: 2px;
        border-width: 1px;
        border-style: solid;
        text-align: center;
        line-height: 16px;
    }

.checkbox--23OsqqyZ9T input + span > * {
            position: relative;
            top: -1px;
            left: -1px;
            font-size: 16px;
        }

.checkbox--23OsqqyZ9T.dark--1UK4eHwUJ1 input + span {
        border-color: #112a46;
        color: #fff;
    }

.checkbox--23OsqqyZ9T.dark--1UK4eHwUJ1 input:checked + span {
        background: #112a46;
    }

.checkbox--23OsqqyZ9T.light--9aKExahegY input + span {
        border-color: #fff;
        color: #112a46;
    }

.checkbox--23OsqqyZ9T.light--9aKExahegY input:checked + span {
        background: #fff;
