/**
  Variables are named after http://chir.ag/projects/name-that-color/
  ATTENTION ATTENTION: Please order this list alphabetically
*/

.spinner--11pJjXxTGj {
    position: relative;
    margin: auto;
}

.double-bounce1--m_GIqbA464,
.double-bounce2--3CyFjMpLRM {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #585858;
    opacity: .6;
    position: absolute;
    top: 0;
    left: 0;
    will-change: transform;
    -webkit-animation: bounce--hP928t4DvA 2s infinite ease-in-out;
            animation: bounce--hP928t4DvA 2s infinite ease-in-out;
}

.double-bounce2--3CyFjMpLRM {
    -webkit-animation-delay: -1s;
            animation-delay: -1s;
}

@-webkit-keyframes bounce--hP928t4DvA {
    0%,
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }

    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes bounce--hP928t4DvA {
    0%,
    100% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }

    50% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
