.sketch-picker {
        -webkit-box-shadow: none !important;
                box-shadow: none !important;
        border-radius: 0 0 3px 3px !important;
        border: 1px solid #ccc;
        color: #585858;
    }

.sketch-picker input {
            border-radius: 3px;
            font-size: 12px;
            color: #000;
            text-align: center;
        }

.sketch-picker span {
            width: 80%;
        }
